/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

nav {
  background-color: #1a1a1a;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 0 1.5rem 0;
}
nav .logo a {
  margin: 0 5rem 0 0;
  color: #fff;
  font-size: 3rem;
  text-transform: uppercase;
}
nav .logo a:hover {
  color: darkred;
  cursor: pointer;
}
nav .navegacion a {
  color: #fff;
}
nav .navegacion a:hover {
  color: darkred;
  cursor: pointer;
}
nav .navegacion ul {
  list-style: none;
  display: flex;
  gap: 2rem;
  font-size: 2rem;
}
@media (max-width: 1024px) {
  nav {
    display: grid;
    grid-template-columns: 100%;
  }
  nav .logo {
    grid-row: 1/2;
    text-align: center;
  }
  nav .logo a {
    margin: 0;
  }
}
@media screen and (max-width: 698px) {
  nav .navegacion ul {
    flex-direction: column;
    text-align: center;
  }
}

.contenido {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: 50% 50%;
  column-gap: 2rem;
}
@media (max-width: 1024px) {
  .contenido {
    grid-template-columns: 100%;
  }
}
.contenido .contenido-izquerda_top {
  display: flex;
  gap: 1rem;
}
.contenido .contenido-izquerda_top img {
  width: 13rem;
  border-radius: 1rem;
}
.contenido .contenido-izquerda_middle {
  margin: 10rem 0 10rem 0;
}
.contenido .contenido-izquerda_bottom img {
  width: 80rem;
  height: 40rem;
  border-radius: 2rem;
  margin-bottom: 10rem;
}
@media (max-width: 1024px) {
  .contenido .contenido-izquerda_bottom img {
    width: 100%;
  }
}

.contenido-derecha {
  display: flex;
  flex-direction: column;
  gap: 10rem;
  align-items: flex-start;
  margin: 0 auto 0 auto;
}
.contenido-derecha .contenido-producto {
  display: flex;
  align-items: center;
  width: 50rem;
  border-radius: 1rem;
  color: #fff;
  justify-content: space-between;
  background-color: #1a1a1a;
}
.contenido-derecha .contenido-producto img {
  width: 5rem;
  fill: #fff;
  margin-right: 2rem;
  padding-left: 2rem;
  border-left: solid white 1px;
}
.contenido-derecha .contenido-producto img:hover {
  cursor: pointer;
}
.contenido-derecha .contenido-producto h2 {
  margin: 0 0 0 1rem;
}
.contenido-derecha:last-child {
  margin-bottom: 10rem;
}